<template>
    <div>
        <b-form-input type="text" v-model="displayValue" @blur="isInputActive = false" @focus="isInputActive = true" :disabled="disabled" :class="customClass" v-on:keyup.enter="focus($event)"/>
    </div>
</template>

<script>
export default {
    name: 'CustomInput',

    props: ["value", "disabled", "alias", "type", "classes"],

    data() {
        return {
            isInputActive: false
        }
    },

    methods: {
        focus(event) {
            let interval = 0;

            switch(this.alias) {
                case 'average_hourly_rate': 
                case 'rounded_labor_days': 
                    interval = 1;
                    break;
                case 'burden_percent': 
                case 'labor_target_percent': 
                    interval = 2;
                    break;
                case 'epic_work_order_hours': 
                    interval = 3;
                    break;
                case 'billable_hours_day': 
                    interval = 4;
                    break;
            }

            let prependKey = '__BVID__';
            let splittedId = event.target.id.split(prependKey);
            let id = prependKey + (parseInt(splittedId[1]) + (interval * 2));

            if(interval !== 0 && id) {
                document.getElementById(id).focus();
            }
        }
    },

    computed: {
        displayValue: {
            get: function() {
                // Note: we cannot set this.value as it is a "prop". It needs to be passed to parent component
                let value = this.value;

                if (this.isInputActive) {
                    // Cursor is inside the input field. unformat display value for user
                    return value.toString();
                } else {
                    if(!isFinite(value)) {
                        value = 0; // eslint-disable-line
                    } 

                    let format = value.toFixed(2).replace(/(\d)(?=(\d{3})+(?:\.\d+)?$)/g, "$1,");

                    switch(this.type) {
                        case 'dollar':
                            format = "$ " + value.toFixed(2).replace(/(\d)(?=(\d{3})+(?:\.\d+)?$)/g, "$1,");
                            break;
                        case 'percent': 
                            format = value.toFixed(2) + '%';
                            break;
                        case 'dollar_parenthesis':
                            // format = value < 0
                            //             ? "- $ (" + (value*-1).toFixed(2).replace(/(\d)(?=(\d{3})+(?:\.\d+)?$)/g, "$1,") + ')'
                            //             : "$ (" + value.toFixed(2).replace(/(\d)(?=(\d{3})+(?:\.\d+)?$)/g, "$1,") + ')';
                            format = "$ (" + value.toFixed(2).replace(/(\d)(?=(\d{3})+(?:\.\d+)?$)/g, "$1,") + ')';
                            break;
                    }
                    // User is not modifying now. Format display value for user interface
                    return format;
                }
            },
            set: function(modifiedValue) {
                // Recalculate value after ignoring "$" and "," in user input
                let newValue = parseFloat(modifiedValue.replace(/[^\d\.]/g, "")) // eslint-disable-line
                // Ensure that it is not NaN
                if (isNaN(newValue)) {
                    newValue = 0
                }
                // Note: we cannot set this.value as it is a "prop". It needs to be passed to parent component
                // $emit the event so that parent component gets it
                this.$emit('input', newValue);
            }
        },

        customClass: function() {
            let custom = this.classes === undefined ? '' : this.classes;

            return custom;
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .noBoxShadow {
        border-color: inherit !important;
        -webkit-box-shadow: none !important;
        box-shadow: none !important;
    }

    .bg-gray-300 {
        background: ##e9ecef !important;
    }
</style>